import STATE_core from '@/store/STATE_core.js';
import STATE_params from '@/store/STATE_params.js';
import Show_hide from '@/logic/Show_hide.js';

/**
 * Hide_hardcoded
*/
class Hide_hardcoded
{
	/**
	 * zp_person_boolean
	*/
	zp_person_boolean(input)
	{
		// run only in ZP and step3, others are always shown
		if (STATE_core().product_group_id() !== 4/*zp*/ || STATE_core().current_step() !== 3) {
			return true;
		}

		// important selector
		let pocet_osob = STATE_params().get_call_param('zp_poistenie_pocetosob');
		// just a fallback
		if (pocet_osob === '') {
			return true;
		}
		pocet_osob = parseInt(pocet_osob, 10);

		// get person_index from id
		// id example: zp_osoba2_opravnenaosoba1_meno
		let id = Object.keys(input)[0];
		let start = id.indexOf('_osoba') + '_osoba'.length;
		let person_index = id.substring(start, start + 1);
		person_index = parseInt(person_index, 10);

		// leave displayed inputs with person lower, same or without(NaN)
		if (isNaN(person_index) || person_index <= pocet_osob) {
			return true;
		}

		// hide inputs with person higher
		return false;
	}


	/**
	 * zp_checkboxes_boolean
	*/
	zp_checkboxes_boolean(input)
	{
		// run only in ZP and step3, others are always shown
		if (STATE_core().product_group_id() !== 4/*zp*/ || STATE_core().current_step() !== 3) {
			return true;
		}

		// selected product
		let contract = STATE_params().get_selected_product().next_contract;

		// Allianz + NN
		if (contract === 'view_zp_allianz' || contract === 'view_zp_nn') {
			return this.display_blocks_under_this_checkbox(input);
		}

		// Axa
		if (contract === 'view_zp_axa') {
			return this.display_blocks_by_checkbox_count(input);
		}

		// other products are not affected
		return true;
	}


	/**
	 * display_blocks_under_this_checkbox
	*/
	display_blocks_under_this_checkbox(input)
	{
		// there are blocks of inputs, one parent question checkbox, when checked few answer inputs are shown
		// zp_allianz_osoba1_dotaznik_otazka6
		// zp_allianz_osoba1_dotaznik_odpoved6_1
		// zp_allianz_osoba1_dotaznik_odpoved6_2
		// zp_allianz_osoba1_dotaznik_odpoved6_3
		// {product_prefix}_osoba{person_index}_dotaznik_{otazka/odpoved}{block_number}_{odpoved_order}

		// run only for odpoved inputs
		let id = Object.keys(input)[0];
		if (id.indexOf('_dotaznik_odpoved') === -1) {
			return true;
		}

		// get product_prefix from id
		let end = id.indexOf('_osoba');
		let product_prefix = id.substring(0, end);

		// get person_index from id
		let start = id.indexOf('_osoba') + '_osoba'.length;
		let person_index = id.substring(start, start + 1);

		// get block_number from id
		start = id.indexOf('_dotaznik_odpoved') + '_dotaznik_odpoved'.length;
		let block_number = id.substring(start, start + 2); // last 2 digits
		block_number = block_number.replace('_', ''); // but could be one digit ending with underscore

		// is otazka checked ?
		let target_id = product_prefix + '_osoba' + person_index + '_dotaznik_otazka' + block_number;
		let checkbox_value = STATE_params().get_form_value(target_id);

		// if yes, show odpoved
		if (checkbox_value === '1') {
			return true;
		}

		// otherwise hidden, and value has to be purged
		Show_hide.purge_helper(input[id].type, id);
		return false;
	}


	/**
	 * display_blocks_by_checkbox_count
	*/
	display_blocks_by_checkbox_count(input)
	{
		// each checked question checkbox shows one answer input, count checked and show that many answer inputs
		console.log('ERROR: show_hide for view_zp_axa is not yet implemented');

		return true;
	}
}

export default new Hide_hardcoded;
